import React from "react";

import { graphql, useStaticQuery } from "gatsby";

import useEmblaCarousel from "embla-carousel-react";
import Autoplay from "embla-carousel-autoplay";

import { IReviewsQueryResult } from "../../types/review.type";

import Border from "../../assets/common/border.svg";
import Review from "./review";
import TemplateSection from "../common/templateSection";

const ReviewsSection = () => {
  const [emblaRef, emblaApi] = useEmblaCarousel(
    { loop: true, dragFree: true, duration: 30000, align: 0 },
    [
      Autoplay({
        delay: 0,
        stopOnInteraction: false,
        stopOnMouseEnter: true,
      }),
    ]
  );

  const reviewsData = useStaticQuery(query);

  const reviews = (
    reviewsData?.allMarkdownRemark?.nodes?.length
      ? reviewsData.allMarkdownRemark.nodes
      : null
  ) as IReviewsQueryResult[] | null;

  return (
    <>
      {reviews ? (
        <>
          <Border className="w-full" />
          <TemplateSection
            fullWidth
            title={"our clients' testimonials"}
            className="bg-secondary-100 text-main-100 w-full"
          >
            <div className="w-full overflow-hidden min-h-max" ref={emblaRef}>
              <div
                id="container"
                className="flex"
                onMouseEnter={() => {
                  emblaApi?.internalEngine().animation.stop();
                }}
                onMouseLeave={() => {
                  emblaApi?.internalEngine().animation.start();
                }}
              >
                {reviews.map(({ id, frontmatter }) => (
                  <div
                    key={id}
                    className="flex shrink-0 min-w-0 cursor-pointer basis-full sm:basis-5/6 md:basis-4/6 lg:basis-1/2 xl:basis-2/5 3xl:basis-1/3 4xl:basis-1/4 rounded-2xl border-2 border-dashed border-main-10 mx-24 top-0 bottom-0"
                  >
                    {<Review data={frontmatter} />}
                  </div>
                ))}
              </div>
            </div>
          </TemplateSection>
          <Border className="w-full" />
        </>
      ) : null}
    </>
  );
};

export default ReviewsSection;

export const query = graphql`
  query {
    allMarkdownRemark(filter: { fields: { sourceName: { eq: "reviews" } } }) {
      nodes {
        id
        frontmatter {
          company
          location
          person
          position
          text
          image {
            childImageSharp {
              gatsbyImageData(
                height: 112
                placeholder: BLURRED
                formats: [AUTO, AVIF, WEBP]
              )
            }
          }
        }
      }
    }
  }
`;
